<template>
  <div class="cms-login__container">
    <div class="cms-login__inner">
      <div class="cms-login__logo">
        <img :src="$cms.filterUrl(storeInfo.platFormLoginLogoUrl)" class="logo__img">
      </div>
      <div class="login-form">
        <form>
          <div class="login-form__group">
            <span class="login-form__suffix">
              <i
                class="iconfont icon-yonghu1 login-icon"
                :class="activeName=='name'?'login-icon--active':''"
              ></i>
            </span>
            <input
              type="text"
              @keyup.enter="login"
              @input="changeValue"
              autofocus="autofocus"
              v-model="formData.userName"
              placeholder="请输入用户名"
              @focus="getFocus('name')"
              class="login-form__input"
              spellcheck="false"
            >
          </div>
          <div class="login-form__group">
            <span class="login-form__suffix">
              <i
                class="iconfont icon-mima1 login-icon"
                :class="activeName=='password'?'login-icon--active':''"
              ></i>
            </span>
            <input
              type="password"
              @keyup.enter="login"
              @input="changeValue"
              @focus="getFocus('password')"
              v-model="formData.password"
              placeholder="请输入密码"
              class="login-form__input"
            >
          </div>
          <!-- 验证码 -->
          <div class="login-form__group" v-if="isCaptcha">
            <span class="login-form__suffix">
              <i
                class="iconfont icon-dunpai login-icon"
                :class="activeName=='captcha'?'login-icon--active':''"
              ></i>
            </span>
            <input
              type="text"
              @input="changeValue"
              @focus="getFocus('captcha')"
              placeholder="请输入验证码"
              v-model="formData.captcha"
              class="login-form__input login-form__input--captcha"
            >
            <img :src="captchaImg" class="login-form__captcha" @click="getCaptcha">
          </div>
          <!-- 错误提示 -->
          <div class="login-form__error animated" v-if="errorStatus">{{errorMsg}}</div>
          <!-- 记住密码-->
          <!-- 登录按钮 -->
          <button
            type="button"
            class="login-form__submit"
            @click="login"
            :class="
          isLogin?'login-form__submit--active':''
          "
          >
            <span v-if="isLogin">
              <i class="el-icon-loading"></i>
              登录中...
            </span>
            <span v-else>登录</span>
          </button>
        </form>
      </div>
    </div>
    <div class="copyright">{{storeInfo.icp}}</div>
  </div>
</template>
<script>
import { globalInfo, getCaptcha } from './../api/global.js'
export default {
  data() {
    return {
      formData: {
        userName: '',
        password: '',
        captcha: '',
        sessionId: ''
      },
      captchaImg: '',
      rember: false,
      isCaptcha: false,
      activeName: 'name',
      errorStatus: false,
      errorMsg: '',
      isLogin: false,
      storeInfo: {}
    }
  },
  methods: {
    getGobalInfo() {
      globalInfo().then(res => {
        this.storeInfo = res.data
      })
    },
    login() {
      if (!this.isLogin) {
        this.errorStatus = false
        this.errorMsg = ''
        if (this.formData.userName === '') {
          this.errorStatus = true
          this.errorMsg = '请输入用户名'
          return false
        }
        if (this.formData.password === '') {
          this.errorStatus = true
          this.errorMsg = '请输入密码'
          return false
        }
        this.isLogin = true
        this.$store.dispatch('login', this.formData).then(res => {
          if (res.code === 200) {
            this.$store.dispatch('setRouter', res.data.auth).then(route => {
              this.$router.addRoutes(route)
              this.$routerLink('/index')
            })
          } else {
            this.isLogin = false

            if (res.code === 10512) {
              this.errorStatus = true
              this.errorMsg = '账户名或者密码错误'
              if (res.data.nextNeedCaptcha) {
                this.isCaptcha = true
              }
              this.getCaptcha()
            }
            if (res.code === 504) {
              this.isCaptcha = true
              this.errorStatus = true
              this.errorMsg = '验证码错误'
              this.getCaptcha()
            }
          }
        }).catch(e => {
          this.isLogin = false
          this.$errorMessage('系统异常')
        })
      }
    },
    changeValue() {
      this.errorStatus = false
    },
    getFocus(activeName) {
      this.activeName = activeName
    },
    getCaptcha() {
      getCaptcha().then(res => {
        this.captchaImg = 'data:image/jpeg;base64,' + res.img
        this.formData.sessionId = res.sessionId
      })
    }
  },
  created() {
    this.getGobalInfo()
    this.getCaptcha()
  }
}
</script>



